<template>
	<div style="width: 100%;overflow:hidden;position: relative;height: 800px;" id="banner">
		<ul :style="{width:'200%',overflow:'hidden',left:-index*100+'%'}">
			<li :style="{width:'50%',float:'left',listStyle:'none'}" v-for="i in arr">
				<img :src="i" style="width: 100%;" alt="">
			</li>
		</ul>
	</div>
</template>

<script>
	export default {
		mounted(){
			set:{
				setInterval(()=>{
					this.index++;
					if(this.index>this.num-1){
						this.index=0;
					}
				},2000)
			}
		},
		data() {
			return {
				arr:[
					'https://ccdn.goodq.top/caches/e74a3dc129ab17d27b9988d4cf40e056/aHR0cDovLzU5MTQzN2U3NDM4YWQudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNy8wNS84OWFmNGNkOWUyNGI3NGViZGM4ODJlZDZiMjFiMGEzYS5qcGc_p_p100_p_3D.jpg',
					'https://ccdn.goodq.top/caches/e74a3dc129ab17d27b9988d4cf40e056/aHR0cDovLzU5MTQzN2U3NDM4YWQudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNy8wNS83YzIxZTM3ZmY2OWIwYTIwMWIwMTQ2YTBiZTc2YTkyOC5qcGc_p_p100_p_3D.jpg'
				],
				num:2,
				index:0
			};
		}
	}
</script>

<style>
	#banner ul{
		position: absolute;
		left: 0;
		top: 0;
		transition: 1s;
	}
	
</style>



