import Vue from 'vue'
import Router from 'vue-router'
import restaurant from './restaurant.vue'
import livingRoom from './livingRoom.vue'
import bedroom from './bedroom.vue'
import kitchen from './kitchen.vue'
import bathroom from './bathroom.vue'
import blog from './blog.vue'
import about from './about.vue'
import index from './index.vue'
import more from './more.vue'
import login from './login.vue'
import login_up from './login_up.vue'
import shoppingMall from './shoppingMall.vue'
import shoppingCart from './shoppingCart.vue'

Vue.use(Router)
export default new Router({
	routes:[
		{
			path:'/restaurant',
			name:'restaurant',
			component:restaurant
		},{
			path:'/livingRoom',
			name:'livingRoom',
			component:livingRoom
		},{
			path:'/bedroom',
			name:'bedroom',
			component:bedroom
		},{
			path:'/kitchen',
			name:'kitchen',
			component:kitchen
		},{
			path:'/bathroom',
			name:'bathroom',
			component:bathroom
		},{
			path:'/blog',
			name:'blog',
			component:blog
		},{
			path:'/about',
			name:'about',
			component:about
		},{
			path:'/',
			name:'index',
			component:index
		},{
			path:'/more',
			name:'more',
			component:more
		},{
			path:'/login',
			name:'login',
			component:login
		},{
			path:'/login_up',
			name:'login_up',
			component:login_up
		},{
			path:'/shoppingMall',
			name:'shoppingMall',
			component:shoppingMall
		},{
			path:'/shoppingCart',
			name:'shoppingCart',
			component:shoppingCart
		}
	]
})



