<template>
	<div>
		<div style="width: 100%;background-color: #ffffff;color:#666666;background: #f4f4f4;">
			<div style="max-width: 1024px;margin: 0 auto;padding: 10px 40px;">
				<span style="color:#969696;font-size:14px;font-family:;margin-right:10px;"><a href="#/" style="color:#707070;font-size:14px;text-decoration: none;">首页</a></span>
				<span style="color:#969696;font-size:14px;font-family:;margin-right:10px;">/</span>
				<span style="color:#969696;font-size:14px;font-family:;margin-right:10px;">{{g_json1.type}}</span>
				<span style="color:#969696;font-size:14px;font-family:;margin-right:10px;">/</span>
				<span style="color:#969696;font-size:14px;font-family:;margin-right:10px;">{{g_json1.name}}</span>
			</div>
			<div style="max-width: 1024px;margin: 0 auto;background: #fff;padding: 30px 40px 0;overflow: hidden;">
				<div style="width: 83.3333333334%;float: left;overflow: hidden;">
					<div style="width: 35%;float: left;">
						<div style="width: 100%;padding: 6px;border: 1px solid #e2e2e2;">
							<img :src="g_json1.img" style="width: 100%;" alt="">
						</div>
						<div style="width: 100%;padding-top: 16px;">
							<div style="width: 22.15%;margin-right: 3.8%;">
								<img :src="g_json1.img" style="width: 100%;" alt="">
							</div>
						</div>
					</div>
					<div style="width: 61%; float: right;">
						<p style="color: #555555;font-size: 22px;margin-bottom: 15px;">{{g_json1.name}}</p>
						<div style="width: 80px;height: 3px;margin: 15px 0;background-color: #dddddd;"></div>
						<p style="font-size: 26px;font-weight: normal;color: #ed4917;font-family: Raleway;line-height: 26px;margin-bottom: 15px;">¥{{g_json1.q}}</p>
						<a href="#/shoppingCart" style="text-decoration: none;"><button style="background-image: none;font-size: 16px;font-weight: normal;color: #ffffff;font-family: 微软雅黑;border-color: #fb5e1a;background-color: #fb5e1a;padding-top: 12px;padding-bottom: 12px;padding-left: 65px;padding-right: 65px;outline: none;border: none;" @click="a_jsonArr(json1)">
							加入购物车
							</button></a>
						<i style="display: inline-block;width: 15px;"></i>
						<span style="display: inline-block;border:1px solid #d6d6d6;">
							<button style="border-color: #d6d6d6;height: 42px!important;width: 32px!important;border: none;outline: none;background: #fff;" @click="num>0?num--:0">-</button>
							<input type="text" v-model="num" value="" style="border-color: #d6d6d6;height: 42px!important;width: 32px!important;border:1px solid #d6d6d6;text-align: center;outline: none;">
							<button style="border-color: #d6d6d6;height: 42px!important;width: 32px!important;border: none;outline: none;background: #fff;" @click="num++">+</button>
						</span>
						<p style="font-size: 14px;font-weight: normal;color: #bbbbbb;font-family: 微软雅黑;border-color: #dddddd;">
							分类：<a :href="'#/'+g_json1.url" style="color: #fb5e1a;text-decoration: none;">{{g_json1.type}}</a>
						</p>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapActions,mapGetters} from 'vuex'
	export default {
		methods: {
			...mapActions(['a_jsonArr']),
		},
		
		computed: {
			...mapGetters(['g_json1','g_jsonArr'])
		},
		mounted(){
			this.json1=this.g_json1;
			this.json1.s=this.num;
			console.log(this.json1)
		},
		watch:{
			num(a,b){
				this.json1.s=a
			}
		},
		data() {
			return {
				num:1,
				json1:''
			};
		}
	}
</script>

<style>
*{padding: 0;margin: 0;box-sizing: border-box;}
</style>



