<template>
	<div id="shoppingCart">
		<div style="max-width: 1024px;padding: 80px 0;overflow: hidden;margin: 0 auto;">
			<div style="width: 66.667%;float: left;">
				<div style="width: 100%;padding-right: 30px;border-right: 1px solid #ddd;float: left;">
					<div style="width: 100%;overflow: hidden;border-bottom: 3px solid #ddd;">
					<div style="width: 58%;float: left;font-size: 16px;font-weight: normal;color: rgb(85, 85, 85);font-family: 微软雅黑;padding-top: 20px;padding-bottom: 20px;padding-left: 12px;padding-right: 12px;">商品</div>
					<div style="width: 12%;float: left;font-size: 16px;font-weight: normal;color: rgb(85, 85, 85);font-family: 微软雅黑;padding-top: 20px;padding-bottom: 20px;padding-left: 12px;padding-right: 12px;">价格</div>
					<div style="width: 16%;float: left;font-size: 16px;font-weight: normal;color: rgb(85, 85, 85);font-family: 微软雅黑;padding-top: 20px;padding-bottom: 20px;padding-left: 12px;padding-right: 12px;">数量</div>
					<div style="width: 12%;float: left;font-size: 16px;font-weight: normal;color: rgb(85, 85, 85);font-family: 微软雅黑;padding-top: 20px;padding-bottom: 20px;padding-left: 12px;padding-right: 12px;text-align: right;">合计</div>
					</div>
					<div style="width: 100%;overflow: hidden;display: flex;align-items: center;" v-for="(i,$index) in g_jsonArr">
						<div style="padding-top: 8px;padding-bottom: 8px;padding-left: 8px;padding-right: 8px;float: left;">
							<i style="font-size: 18px;height: 25px;border-radius: 50%;border: 2px solid #ddd;color: #ddd;display: flex;justify-content: center;align-items: center;width: 25px;font-style: normal;" @click="a_dataArr(i)">x</i>
						</div>
						<div style="padding-top: 8px;padding-bottom: 8px;padding-left: 8px;padding-right: 8px;float: left;width: 90px;">
							<img :src="i.img" style="width: 100%;" alt="">
						</div>
						<div style="width: 35%;padding: 0 30px;font-size: 14px;font-weight: normal;color: #000000;">{{i.name}}</div>
						<div style="    font-size: 14px;font-weight: normal;color: #000000;padding: 8px;padding-left:16px ;">¥{{i.q}}</div>
						<div style="min-width: 100px;padding: 8px;">
							<button style="float: left;border:1px solid #d6d6d6;border-color: #d6d6d6;height: 42px!important;width: 32px!important;outline: none;background: #fff;" @click="btn1($index)">-</button>
							<input type="text" :value="i.s" style="border-color: #d6d6d6;height: 42px!important;width: 32px!important;border:1px solid #d6d6d6;text-align: center;outline: none;float: left;">
							<button style="float: left;border:1px solid #d6d6d6;border-color: #d6d6d6;height: 42px!important;width: 32px!important;outline: none;background: #fff;" @click="btn($index)">+</button>
						</div>
						<div style="font-size: 14px;font-weight: bold;color: #fb5e1a;font-family: Raleway;padding: 8px;padding-left:30px ;">¥{{i .h}}</div>
					</div>
				</div>
			</div>
			<div style="width: 33.3333%;float: left;padding-left: 30px;">
				<p style="font-size: 16px;font-weight: bold;color: #555555;font-family: 微软雅黑;padding-top: 4px;padding-bottom: 4px;border-bottom:3px solid #dddddd;margin-bottom: 10px;">购物车总计</p>
				<p style="display: flex;justify-content: space-between;padding-bottom: 12px;padding-top: 4px;">
					<span style="font-size: 14px;font-weight: bold;color: #777777;">购物车小计</span>
					<span style="font-size: 14px;color: #777777;">¥{{num}}</span>	
				</p>
				<p style="display: flex;justify-content: space-between;padding-bottom: 12px;padding-top: 4px;">
					<span style="font-size: 14px;font-weight: bold;color: #777777;">运费</span>
					<span style="font-size: 14px;color: #777777;">免费送货</span>	
				</p>
				<p style="display: flex;justify-content: space-between;padding-bottom: 12px;padding-top: 4px;border-top: 1px solid #ddd;    border-bottom: 3px solid #ddd;">
					<span style="font-size: 14px;font-weight: bold;color: #777777;">订单总计</span>
					<span style="font-size: 14px;color: #777777;">¥{{num}}</span>	
				</p>
				<button style="margin: 20px 0;width: 100%;height: 42px;display: flex;justify-content: center;align-items: auto;font-size: 16px;font-weight: normal;color: #ffffff;font-family: 微软雅黑;border-color: #fb5e1a;background-color: #fb5e1a;outline: none;">前往收银台</button>
				<p style="font-size: 15px;font-weight: bold;color: #555555;font-family: 微软雅黑;padding-top: 4px;padding-bottom: 4px;border-bottom:3px solid #dddddd;margin-bottom: 10px;">优惠券：</p>
				<input type="text" style="font-size: 15px;font-weight: normal;color: #919191;font-family: 微软雅黑;border-color: #c9c9c9;background-color: transparent;padding-top: 7px;padding-bottom: 7px;border: none;outline: none;width: 100%;border: 1px solid #c9c9c9;margin-bottom: 15px;" placeholder="优惠券代码">
				<button id="btn11" style="font-size: 15px;font-weight: normal;color: #fb5e1a;font-family: 微软雅黑;border-color: #fb5e1a;background-color: #ffffff;padding-top: 8px;padding-bottom: 8px;width: 100%;display: flex;align-items: center;justify-content: center;border: 1px solid;">使用优惠券</button>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapActions,mapGetters} from 'vuex'
	export default {
		methods: {
			...mapActions(['a_jsonArr','a_dataArr']),
			btn(a){
				this.g_jsonArr[a].s++;
				this.g_jsonArr[a].h=this.g_jsonArr[a].s*this.g_jsonArr[a].q
				var num=this.g_jsonArr[a];
				this.g_jsonArr.splice(a,1,num)
				var num1=0;
				for(var i=0;i<this.g_jsonArr.length;i++){
					num1+=this.g_jsonArr[i].h
				}
				this.num=num1
			},
			btn1(a){
				this.g_jsonArr[a].s--;
				
				if(this.g_jsonArr[a].s<0){
					this.g_jsonArr[a].s=0
				}
				this.g_jsonArr[a].h=this.g_jsonArr[a].s*this.g_jsonArr[a].q
				var num=this.g_jsonArr[a];
				this.g_jsonArr.splice(a,1,num)
				var num1=0;
				for(var i=0;i<this.g_jsonArr.length;i++){
					num1+=this.g_jsonArr[i].h
				}
				this.num=num1
			}
		},
		
		computed: {
			...mapGetters(['g_json1','g_jsonArr'])
		},
		created(){
			for(var i=0;i<this.g_jsonArr.length;i++){
				this.num+=this.g_jsonArr[i].h
			}
		},
		data() {
			return {
				num:0
			};
		}
	}
</script>

<style>
*{padding: 0;margin: 0;box-sizing: border-box;}
#btn11:hover{
	background: #ed4917;
}
</style>



