import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)


export default new Vuex.Store({
	state: {
		jsonName: {
			'name': '餐厅家具',
			'yin': '  / diningroom furniture'
		},
		teb: '全部',
		json1: {
			name: '扶手椅',
			q: ' 59.00',
			type: '卧室',
			type1: '全部',
			url: 'bedroom',
			img: 'https://ccdn.goodq.top/caches/e74a3dc129ab17d27b9988d4cf40e056/aHR0cDovLzU5MTQzN2U3NDM4YWQudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNy8wNS9hZGRhZDI1YTZkNWM2M2I1NjNjN2U5NTk2MGNlZDA3Mi00MDB4NDAwLmpwZw_p_p100_p_3D_p_p100_p_3D.jpg'
		},
		jsonArr: []
	},
	getters: {
		g_jsonName(state) {
			return state.jsonName
		},
		g_jsonArr(state) {
			return state.jsonArr
		},
		g_json1(state) {
			return state.json1
		},
		g_teb(state) {
			return state.teb
		}
	},
	mutations: {
		m_jsonName(state, data) {
			state.jsonName = data
		},
		m_dataArr(state,data){
			for(var i=0;i<state.jsonArr.length;i++){
				if(state.jsonArr[i].name == data.name){
					state.jsonArr.splice(i,1)
				}
			}
			
		},
		m_jsonArr(state, data) {
			var sp = true
			for (var i = 0; i < state.jsonArr.length; i++) {
				if (state.jsonArr[i].name == data.name) {
					sp = false
					state.jsonArr[i].s+=data.s;
					state.jsonArr[i].h=state.jsonArr[i].s*state.jsonArr[i].q
					var num = state.jsonArr[i]
					state.jsonArr.splice(i, 1, num)
					return false
				}
			}
			if (sp) {
				data.h=data.q*data.s;
				state.jsonArr.push(data)
			}
			console.log(state.jsonArr)
		},
		m_json1(state, data) {
			state.json1 = data
		},
		m_teb(state, data) {
			state.teb = data
		}
	},
	actions: {
		a_jsonName({
			commit
		}, data) {
			commit('m_jsonName', data)
		},
		a_json1({
			commit
		}, data) {
			commit('m_json1', data)
		},
		a_jsonArr({
			commit
		}, data) {
			commit('m_jsonArr', data)
			console.log(data)
		},
		a_dataArr({
			commit
		}, data) {
			commit('m_dataArr', data)
			console.log(data)
		},
		a_teb({
			commit
		}, data) {
			commit('m_teb', data)
			console.log(data)
		}
	}
})



