<template>
	<div id="navs" style="z-index: 999999;">
		<div style="float: left; padding: 10px 10px 10px 0px;">
			<img src="https://ccdn.goodq.top/caches/e74a3dc129ab17d27b9988d4cf40e056/aHR0cDovLzU5MTQzN2U3NDM4YWQudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNy8wNS82YWY5OGRkODYzYjcxZGNjNmU0Y2E3OWM4YzdmNjNkMS5wbmc_p_p100_p_3D.png"
			 style="width: 165px">
		</div>
		<ul id="navs_list" style="float: right;overflow: hidden;list-style: none;">
			<li v-for="(i,$index) in arr" style="float: left;" @click="index=$index">
				<a :class="index==$index?'active':''" :href="'#'+i.url" style="padding: 20px 12px 21px 12px;color: #8e8e8e;font-size: 14px;display: block;text-decoration: none;min-width: 50px;text-align: center;box-sizing: initial;"
				 @click="a_jsonName(i.json),a_teb(i.name)">{{i.name}}</a>
			</li>
		</ul>
	</div>
</template>

<script>
	import {
		mapActions,
		mapGetters
	} from 'vuex'
	export default {
		methods: {
			...mapActions(['a_jsonName', 'a_teb']),
		},
		
		computed: {
			...mapGetters([''])
		},

		data() {
			return {
				index: 9,
				arr: [{
					name: '客厅',
					url: '/livingRoom',
					json: {
						'name': '客厅家具',
						'yin': '  / bathroom furniture'
					}
				}, {
					name: '卧室',
					url: '/bedroom',
					json: {
						'name': '卧室家具',
						'yin': '  / bedroom furniture'
					}
				}, {
					name: '餐厅',
					url: '/restaurant',
					json: {
						'name': '餐厅家具',
						'yin': '  / diningroom furniture'
					}
				}, {
					name: '厨房',
					url: '/kitchen',
					json: {
						'name': '餐厅家具',
						'yin': '  / diningroom furniture'
					}
				}, {
					name: '浴室',
					url: '/bathroom',
					json: {
						'name': '浴室配件',
						'yin': '  / bathroom furniture'
					}
				}, {
					name: '博客',
					url: '/blog'
				}, {
					name: '关于',
					url: '/about'
				}, ]
			};
		}
	}
</script>

<style>
	#navs_list li:hover>a {
		background: linear-gradient(to bottom, #ff6f00, #ff6f00);
		color: #fff !important;
	}

	.active {
		background: linear-gradient(to bottom, #ff6f00, #ff6f00) !important;
		color: #fff !important;
	}
</style>



