<template>
			<div style="padding-top: 10px;padding-bottom: 20px;color:#000000;overflow: hidden;">
				<span style="padding-top: 10px;padding-left: 10px;float: left;">
					<span style="font-family: 微软雅黑;color:#222222;font-weight:bold;font-size: 20px;padding-bottom:10px">{{g_jsonName.name}}</span>
					<span style="font-size:15px;font-style:normal;color:#333;padding:0 0 10px 0;">{{g_jsonName.yin}}</span>
				</span>
				<input type="text" style="width:240px;height: 46px;border: 1px solid #e0e0e0;color: #a8a8a8;float: right;padding: 6px 10px 5px;"
				placeholder="请输入商品名字">
			</div>
</template>

<script>
	import {mapActions,mapGetters} from 'vuex'
	export default {
		methods:{
			...mapActions([''])
		},
		computed:{
			...mapGetters(['g_jsonName'])
		},
		data() {
			return {
				jsonName: this.jsonName
			};
		}
	}
</script>

<style>

</style>



