						Coffee之后，在国内通路上的另一次突破。近年来，生活型态提案店在日本已有快速且成熟的发展，所以统一集团对于引进这样的事业，深具信心。由于统一集团的国外合作伙伴，都是世界一流的业者，这一次也不例外。日本良品计划在日本已有23年的历史，企业文化认真朴实，深受日本消费者的认同，且信誉卓越，所以对这次的合作关系，双方都保持着乐观的看法。</p>
					&nbsp;
					<p style="font-size: 14px;color:#999999;line-height:1.6;">无印良品出品的商品，在设计上是简单无华的，但我们不是一味地在奉行极简主义而已。因为无印良品这个品牌，就像是个空的容器一般，能够接受不同的观点，也能够无拘无束的发挥。我们追求“简约无华”的概念落实在很多事物上，像是在制程中节省能源、产品价格实在、设计造型简单、质地自然…。无印良品希望在各方面都能做到“简约无华”，将价值的真实意义还原。</p>
					<p style="font-size: 14px;color:#999999;line-height:1.6;">无印良品的企业理念是可以跨越国界的，从1980年创立至今，我们都在提倡简单过生活的概念，至今仍持续向世界发声。虽然目前日常生活用品往往出现两极化的倾向~其一是使用新奇的素材或是独特的造型，以独特性增加竞争力的商品。这类商品强调限量发售，或是以品牌形象赢得顾客的好评，它们争取的是喜好名牌、高价位的消费族群。另一种则是尽量地以价取胜~使用最便宜的素材，生产过程也极尽地简单化，在劳资低廉的地区生产，以生产最低价位的商品。</p>
